body {
  background: #8e0e00;
  background: -webkit-linear-gradient(to right, #1f1c18, #8e0e00);
  background: linear-gradient(to right, #1f1c18, #8e0e00);
  width: 100%;
}

* {
  margin: 0;
}

header h1 {
  color: white;
  font-size: 40px;
  text-align: center;
  padding: 1em;
  font-family: "Sedgwick Ave", cursive;
  font-weight: lighter;
}

main {
  margin: 0 auto;
  width: 100%;
  max-width: 1000px;
}

.section {
  padding: 15px;
  width: 100%;
}

.search-box {
  display: block;
  width: 100%;
  padding: 1em;
  opacity: 0.8;
  margin-bottom: 19px;
  font-size: 20px;
  border-radius: 10px;
  transition: 0.4s ease-out;
}

.results {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px;
  margin: auto;
}

.result {
  min-width: 250px;
  padding: 8px;
  width: 31.3%;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.result img:before {
  content: " ";
  background-image: url("https://dailygazette.com/sites/default/files/2018-06/viaport%20theater%20seats.jpg");
  display: block;
  width: 313px;
  height: 415px;
  position: relative;
  z-index: 0;
  margin-bottom: -16px;
}

.result img {
  display: block;
  width: 100%;
}

h3 {
  color: white;
  font-size: 22px;
  transition: 0.4s ease-out;
  padding: 27px;
  text-align: center;
  font-family: "Sedgwick Ave", cursive;
  background-color: #010202;
  font-weight: 500;
  flex: 1 1 100%;
}

.result:hover h3 {
  background: lightslategray;
}
